import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router } from "react-router-dom";

import App from "./components/App";

ReactDOM.render(
  <React.StrictMode>
    <>
      <Router basename="/reading_guide">
        <App />
      </Router>
    </>
  </React.StrictMode>,
  document.getElementById("root")
);
